: 'Baloo Da', cursive;\r\n}\r\n","/*\r\nTemplate Name: Rocker Admin\r\nAuthor: CODERVENT\r\nEmail: codervent@gmail.com\r\nFile: app-style\r\n*/\r\n\r\n/*\r\n  - General\r\n  - Menu Sidebar Wrapper\r\n  - Page Content Wrapper\r\n  - Topbar Header\r\n  - Dropdown Menu\r\n  - User Details\r\n  - Logo\r\n  - SearachBar\r\n  - Cards\r\n  - Modals\r\n  - Buttons\r\n  - User Cards\r\n  - Widgets\r\n  - Forms\r\n  - Tables\r\n  - Alerts\r\n  - Badges\r\n  - Paginations\r\n  - List Groups\r\n  - Nav Tabs & Pills\r\n  - Accordions\r\n  - Background Colors\r\n  - Borders\r\n  - Text colors\r\n  - Shadows\r\n  - CheckBoxes & Radios\r\n  - Pricing Tables\r\n  - Responsive\r\n*/\r\n\r\n/* General */\r\n@-ms-viewport {\r\n    width: device-width;\r\n}\r\n\r\nbody {\r\n    background: #f7f7ff;\r\n    color: #636363;\r\n}\r\n\r\n[tabindex=\"-1\"]:focus {\r\n    outline: 0 !important\r\n}\r\n\r\n::placeholder {\r\n    color: #747f8a !important;\r\n    font-size: 13px;\r\n    opacity: .5 !important; /* Firefox */\r\n}\r\n\r\n:-ms-input-placeholder { /* Internet Explorer 10-11 */\r\n    color: #747f8a !important;\r\n}\r\n\r\n::-ms-input-placeholder { /* Microsoft Edge */\r\n    color: #747f8a !important;\r\n}\r\n\r\n.h1, .h2, .h3, .h4, .h5, .h6, h1, h2, h3, h4, h5, h6 {\r\n    color: #292929;\r\n    font-weight: 600;\r\n    color: #263238;\r\n}\r\n\r\n.h1, h1 {\r\n    font-size: 48px;\r\n    line-height: 52px;\r\n}\r\n\r\n.h2, h2 {\r\n    font-size: 38px;\r\n    line-height: 42px;\r\n}\r\n\r\n.h3, h3 {\r\n    font-size: 30px;\r\n    line-height: 34px;\r\n}\r\n\r\n.h4, h4 {\r\n    font-size: 24px;\r\n    line-height: 28px;\r\n}\r\n\r\n.h5, h5 {\r\n    font-size: 18px;\r\n    line-height: 22px;\r\n}\r\n\r\n.h6, h6 {\r\n    font-size: 14px;\r\n    line-height: 18px;\r\n}\r\n\r\n.display-1 {\r\n    font-size: 6rem\r\n}\r\n\r\n.display-2 {\r\n    font-size: 5.5rem\r\n}\r\n\r\n.display-3 {\r\n    font-size: 4.5rem\r\n}\r\n\r\n.display-4 {\r\n    font-size: 3.5rem\r\n}\r\n\r\n.line-height-0 {