;\r\n    font-size: 15px\r\n}\r\n\r\n.dropdown-menu ul {\r\n    margin-top: 0px;\r\n}\r\n\r\n.dropdown-divider {\r\n    margin: 0;\r\n    border-top: 1px solid rgb(236, 236, 253);\r\n}\r\n\r\n.dropdown-item {\r\n    padding: .70rem 1.5rem;\r\n}\r\n\r\n.dropdown-toggle-nocaret:after {\r\n    display: none\r\n}\r\n\r\n.dropdown-lg .dropdown-menu {\r\n    width: 320px;\r\n    padding: 0\r\n}\r\n\r\n.dropdown-lg .dropdown-menu .list-group-flush:first-child .list-group-item:first-child {\r\n    border-top: 0;\r\n    border-top-left-radius: .25em;\r\n    border-top-right-radius: .25em;\r\n}\r\n\r\n.dropdown-lg .dropdown-menu .list-group-flush:last-child .list-group-item:last-child {\r\n    border-bottom: 0;\r\n    border-bottom-left-radius: .25em;\r\n    border-bottom-right-radius: .25em;\r\n}\r\n\r\n.dropdown-lg .dropdown-menu .media .avatar img {\r\n    width: 55px;\r\n    height: 55px;\r\n    border-radius: 50%;\r\n    border: 2px solid #ffffff;\r\n    -webkit-box-shadow: 0px 5px 25px 0px rgba(0, 0, 0, 0.2);\r\n    -moz-box-shadow: 0px 5px 25px 0px rgba(0, 0, 0, 0.2);\r\n    -ms-box-shadow: 0px 5px 25px 0px rgba(0, 0, 0, 0.2);\r\n    box-shadow: 0px 5px 25px 0px rgba(0, 0, 0, 0.2);\r\n}\r\n\r\n.dropdown-lg .dropdown-menu .media .media-body .msg-title {\r\n    font-size: 14px;\r\n    color: #000;\r\n    font-weight: 600;\r\n    margin-bottom: 0px;\r\n}\r\n\r\n.dropdown-lg .dropdown-menu .media .media-body .msg-info {\r\n    font-size: 13px;\r\n    color: #232323;\r\n    margin-bottom: 0;\r\n    white-space: nowrap;\r\n}\r\n\r\n/* User Details */\r\n.user-profile img {\r\n    width: 40px;\r\n    height: 40px;\r\n    border-radius: 50%;\r\n    box-shadow: 0 16px 38px -12px rgba(0, 0, 0, .56), 0 4px 25px 0 rgba(0, 0, 0, .12), 0 8px 10px -5px rgba(0, 0, 0, .2);\r\n}\r\n\r\n.user-details .media .avatar img {\r\n    width: 50px;\r\n    height: 50px;\r\n    border-radius: 50%;\r\n}\r\n\r\n.user-details .media .media-body .user-title {\r\n    font-size: 14px;\r\n    color: #000